
* {
  margin: 0;
  padding: 0;
}
html,
body {
  width: 100%;
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
}
.container {
  width: 100%;
  height: calc(100% - 50px);
  background: #eee;
}
#tab {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 50px;
  text-align: center;
  line-height: 50px;
}
#tab a {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-right: 1px solid #ccc;
}
#tab a:nth-of-type(4) {
  border: none;
}



































.home-lists {
  width: 80%;
  margin: 0 auto 10px;
}
.home-lists .img-box {
  float: left;
  width: 50px;
  height: 50px;
  background: #000;
}
.home-lists p {
  overflow: hidden;
}